body {
    font-family: Arial, sans-serif;
    background: linear-gradient(to bottom, #007BFF, #00C6FF);
    color: #fff;
    margin: 0;
    padding: 0;
}

nav {
    position: fixed;
    width: 100%;
    background: rgba(0, 123, 255, 0.8);
    padding: 10px 0;
}

nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 20px;
}

nav a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
}